.navbar{
    max-width: 100%;
    background-color: var(--bgColorOne);
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.navbar span{
    color: white;
}

.navbarTitle{
    letter-spacing: .2rem;
}

.addButton > button{
    border: solid 2px white;
    background-color: var(--bgColorTwo);
    font-size: 1.2rem;
    font-weight: bold;
    padding: .1rem .4rem 0 .4rem;
    border-radius: 50px;
}

.menuButton,
.addButton > button{
    cursor: pointer;
    transition: transform .2s ease;
}

.menuButton:hover,
.addButton > button:hover{
    transform: scale(1.2);
}

.menuButton:active,
.addButton > button:active{
    transform: scale(1.1);
}

.githubLogo > a > img {
    width: 30px;
    transition: .2s width ease;
}

a > img {
    width: 100%;
}

.githubLogo > a > img:hover{
    width: 35px;
    opacity: .8;
}`, "",{"version":3,"sources":["webpack://./src/css/navbar.css"],"names":[],"mappings":";AACA;IACI,eAAe;IACf,mCAAmC;IACnC,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;AACvB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,uBAAuB;IACvB,mCAAmC;IACnC,iBAAiB;IACjB,iBAAiB;IACjB,4BAA4B;IAC5B,mBAAmB;AACvB;;AAEA;;IAEI,eAAe;IACf,8BAA8B;AAClC;;AAEA;;IAEI,qBAAqB;AACzB;;AAEA;;IAEI,qBAAqB;AACzB;;AAEA;IACI,WAAW;IACX,0BAA0B;AAC9B;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;IACX,WAAW;AACf","sourcesContent":["\n.navbar{\n    max-width: 100%;\n    background-color: var(--bgColorOne);\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n}\n\n.navbar span{\n    color: white;\n}\n\n.navbarTitle{\n    letter-spacing: .2rem;\n}\n\n.addButton > button{\n    border: solid 2px white;\n    background-color: var(--bgColorTwo);\n    font-size: 1.2rem;\n    font-weight: bold;\n    padding: .1rem .4rem 0 .4rem;\n    border-radius: 50px;\n}\n\n.menuButton,\n.addButton > button{\n    cursor: pointer;\n    transition: transform .2s ease;\n}\n\n.menuButton:hover,\n.addButton > button:hover{\n    transform: scale(1.2);\n}\n\n.menuButton:active,\n.addButton > button:active{\n    transform: scale(1.1);\n}\n\n.githubLogo > a > img {\n    width: 30px;\n    transition: .2s width ease;\n}\n\na > img {\n    width: 100%;\n}\n\n.githubLogo > a > img:hover{\n    width: 35px;\n    opacity: .8;\n}"],"sourceRoot":""}]);
