/* sideBar */
body > div.sideBar{
    display: flex;
    flex-direction: column;
    gap: .7rem;
    padding-top: 2rem;
    width: 250px; 
    transition: width .5s ease-out; 
    align-items: center; 
    color: var(--textColor);
    background-color: var(--bgColorTwo);
}

/* sideBar while collapsed */
body > div.sideBar.collapsed{
    width: 100px;
}

/* Toggle button and its container */
div.sideBar > div:first-child{
    display: flex;
    flex-direction: row ;
    align-items: center;
    justify-content: center;
}

/* toggle sidebar button  */
.sideBar > div > div > button{
    transition: opacity 0.2s ease, transform .5s ease;
}

body > div.sideBar.collapsed > div > div > button.toggleBtn{
    transform: rotate(360deg);
}

/* icons */
.sideBar > div > img{
    transition: transform .35s ease;
}

.sideBar.collapsed > .dashboardBtn > button,
.sideBar.collapsed > .todayBtn > button,
.sideBar.collapsed > .importantBtn > button{
    opacity: 0; 
    pointer-events: none; 
}

.sideBar > div > button{
    opacity: 1; 
    cursor: pointer;
    background-color: transparent;
    font-size: 1.1rem;
    transition: opacity .3s ease;
}

.sideBar.collapsed > div > img{
    transform: translate(23px);
}

/* the buttons within the sideBar */
.sideBar > div {
    min-width: 65%;
    padding: .3rem 0 .3rem .2rem;
    display: flex;
    flex-direction: row-reverse;
    justify-content: start;
}

.sideBar > .sideBarText{
    padding: 1rem 0 !important;
    justify-content: center;
}

.sideBar.collapsed > div{
    max-width: 70%;
    padding: .3rem 0 .3rem 0;
}

.sideBar > div > img,
.toggleBtn > img {
    cursor: pointer;
}

.toggleBtn{
    transform: rotate(180deg);
    padding: 0px;
    max-width: 32px;
    max-height: 32px;
    background-color: transparent;
    display: flex;
}

.toggleBtn > img{
    display: flex;
}

.dashboardBtn,
.todayBtn,
.importantBtn{
    border-radius: 5px;
    border: solid 2px var(--bgColorTwo);
    transition: border .3s ease, background-color .3s ease;
}

.dashboardBtn:hover,
.todayBtn:hover,
.importantBtn:hover{
    border: solid 2px rgba(255, 255, 255, 0.126);
    background-color: rgba(255, 255, 255, 0.103);
    border-radius: 5px;
    transition: border .1s ease, background-color .1s ease;
}

.dashboardBtn:active,
.todayBtn:active,
.importantBtn:active{
    background-color: var(--bgColorTwo);
    transition: background-color .1s ease;
}

.addProject{
    border: dashed 2px var(--textColor);
    border-radius: 5px;
    color: var(--textColor);
    transition: transform .2s ease;
}

.addProject:hover{
    background-color: rgba(255, 255, 255, 0.103);
    transform: scale(1.02);
    border-radius: 5px;
}

.addProject:active{
    background-color:var(--bgColorTwo);
}

.addProject > button {
    cursor: pointer;
    width: 100%;
    padding: .4rem 1rem;
    margin: 0 auto;
}`, "",{"version":3,"sources":["webpack://./src/css/sidebar.css"],"names":[],"mappings":";AACA,YAAY;AACZ;IACI,aAAa;IACb,sBAAsB;IACtB,UAAU;IACV,iBAAiB;IACjB,YAAY;IACZ,8BAA8B;IAC9B,mBAAmB;IACnB,uBAAuB;IACvB,mCAAmC;AACvC;;AAEA,4BAA4B;AAC5B;IACI,YAAY;AAChB;;AAEA,oCAAoC;AACpC;IACI,aAAa;IACb,oBAAoB;IACpB,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA,2BAA2B;AAC3B;IACI,iDAAiD;AACrD;;AAEA;IACI,yBAAyB;AAC7B;;AAEA,UAAU;AACV;IACI,+BAA+B;AACnC;;AAEA;;;IAGI,UAAU;IACV,oBAAoB;AACxB;;AAEA;IACI,UAAU;IACV,eAAe;IACf,6BAA6B;IAC7B,iBAAiB;IACjB,4BAA4B;AAChC;;AAEA;IACI,0BAA0B;AAC9B;;AAEA,mCAAmC;AACnC;IACI,cAAc;IACd,4BAA4B;IAC5B,aAAa;IACb,2BAA2B;IAC3B,sBAAsB;AAC1B;;AAEA;IACI,0BAA0B;IAC1B,uBAAuB;AAC3B;;AAEA;IACI,cAAc;IACd,wBAAwB;AAC5B;;AAEA;;IAEI,eAAe;AACnB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,eAAe;IACf,gBAAgB;IAChB,6BAA6B;IAC7B,aAAa;AACjB;;AAEA;IACI,aAAa;AACjB;;AAEA;;;IAGI,kBAAkB;IAClB,mCAAmC;IACnC,sDAAsD;AAC1D;;AAEA;;;IAGI,4CAA4C;IAC5C,4CAA4C;IAC5C,kBAAkB;IAClB,sDAAsD;AAC1D;;AAEA;;;IAGI,mCAAmC;IACnC,qCAAqC;AACzC;;AAEA;IACI,mCAAmC;IACnC,kBAAkB;IAClB,uBAAuB;IACvB,8BAA8B;AAClC;;AAEA;IACI,4CAA4C;IAC5C,sBAAsB;IACtB,kBAAkB;AACtB;;AAEA;IACI,kCAAkC;AACtC;;AAEA;IACI,eAAe;IACf,WAAW;IACX,mBAAmB;IACnB,cAAc;AAClB","sourcesContent":["\n/* sideBar */\nbody > div.sideBar{\n    display: flex;\n    flex-direction: column;\n    gap: .7rem;\n    padding-top: 2rem;\n    width: 250px; \n    transition: width .5s ease-out; \n    align-items: center; \n    color: var(--textColor);\n    background-color: var(--bgColorTwo);\n}\n\n/* sideBar while collapsed */\nbody > div.sideBar.collapsed{\n    width: 100px;\n}\n\n/* Toggle button and its container */\ndiv.sideBar > div:first-child{\n    display: flex;\n    flex-direction: row ;\n    align-items: center;\n    justify-content: center;\n}\n\n/* toggle sidebar button  */\n.sideBar > div > div > button{\n    transition: opacity 0.2s ease, transform .5s ease;\n}\n\nbody > div.sideBar.collapsed > div > div > button.toggleBtn{\n    transform: rotate(360deg);\n}\n\n/* icons */\n.sideBar > 